@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";
.advantageCards {
    @include padding_left_right;
    background-color: $color_bg_black;
}

.advantageCards__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 16px 0;
    @media (min-width: 1200px) {
        padding: 0 0;
    }
}

.advantageCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 0 260px;
    padding: 16px 0;
    @media (min-width: 1200px) {
        padding: 40px 0;
    }
    &:nth-child(2) {
        @media (min-width: 1200px) {
            order: 2;
        }
    }
    &:nth-child(3) {
        @media (min-width: 1200px) {
            order: 3;
        }
    }
    &:nth-child(4) {
        @media (min-width: 1200px) {
            order: 1;
        }
    }
}

.advantageCard__svg {
    margin-bottom: 16px;
}

.advantageCard__description {
    width: min-content;
    text-align: center;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: $color_text_light;
}